      </button>
      <button id="superscript" type="button" class="option-button script">
        <Icon icon="ooui:superscript-ltr" width="20"/>
      </button>
      <button id="subscript" type="button" class="option-button script">
        <Icon icon="ooui:subscript-ltr" width="20"/>
      </button>

      <!-- List -->
      <button id="insertOrderedList" type="button" class="option-button format">
