      </select>

      <!-- Font -->
      <select id="fontName" class="adv-option-button"
              style="padding: 3px 1px; border: 1px solid; border-radius: 4px; outline: none;">
      </select>
      <select id="fontSize" class="adv-option-button"
              style="padding: 3px 1px; border: 1px solid; border-radius: 4px; outline: none;">
      </select>

      <!-- Color -->
      <div class="input-wrapper" style="position:relative;">
        <input type="color" name="font-color" id="foreColor" class="adv-option-button">
        <label for="foreColor" style="border-bottom: 3px solid;">
          <Icon icon="mdi:format-text-variant"/>
        </label>
      </div>
      <div class="input-wrapper" style="position:relative;">
        <input type="color" name="highlight-color" id="backColor" class="adv-option-button">
        <label for="backColor" style="border-bottom: 3px solid;">
          <Icon icon="mdi:paintbrush"/>
        </label>
      </div>
    </div>
    <div :class="props.class" :style="props.style">
      <div id="text-input" contenteditable="true" @input="(e) => handleInput(e.target.innerHTML)"/>
      <div class="info" style="display:flex; justify-content: space-between; align-items: center; padding: 2px">
        <p>&circledcirc; {{ formatBlock }}</p>
        <small>Created by <a href="https://www.linkedin.com/in/rvnkrwn/" target="_blank" style="text-decoration: none">rvnkrwn</a> </small>
      </div>
    </div>
  </div>
</template>

<script setup>
import {Icon} from "@iconify/vue";
import {defineEmits, defineProps, onMounted, ref} from "vue";

const props = defineProps(["style", "class"]);
const emit = defineEmits(["value"]);

const formatBlock = ref("p");
const handleInput = (value) => {
  emit("value", value);
};


onMounted(() => {
  const optionsButtons = document.querySelectorAll(".option-button");
  const advancedButtons = document.querySelectorAll(".adv-option-button");
  const fontName = document.getElementById("fontName");
  const fontSize = document.getElementById("fontSize");
  const linkButton = document.getElementById("createLink");
  const alignButtons = document.querySelectorAll(".align");
  const formatButtons = document.querySelectorAll(".format");
  const scriptButtons = document.querySelectorAll(".script");

  const fontList = ["Arial", "Times New Roman", "Courier New", "Verdana", "Helvetica", "Comic Sans MS"];


  let fontSizeList = [1, 2, 3, 4, 5, 6, 7];

  window.addEventListener("load", () => initializer());

  // init
  const initializer = () => {
    highlighter(alignButtons, true);
    highlighter(formatButtons, true);
    highlighter(scriptButtons, true);

    fontList.map((font) => {
      let option = document.createElement("option");
      option.value = font;
      option.innerHTML = font;
      fontName.appendChild(option);
    });

    fontSizeList.map((size) => {
      let option = document.createElement("option");
      option.value = `${size}`;
      option.innerHTML = `${size}`;
      fontSize.appendChild(option);
    });
    fontSize.value = fontSizeList[3];
  };
  const highlighter = (className, needRemoval) => {
    className.forEach((button) => {
      button.addEventListener("click", () => {
        // need remove
        if (needRemoval) {
          let isActive = false;

          if (button.classList.contains("active")) {
            isActive = true;
          }

          highlighterRemover(className);
          if (!isActive) {
            button.classList.add("active");
          }

        } else {
          button.classList.toggle("active");
        }
      });
    });
  };

  const highlighterRemover = (className) => {
    className.forEach((button) => {
      button.classList.remove("active");
    });
  };
  //   main
  const modify = (command, showUI, value) => {
    document.execCommand(command, showUI, value);
  };

  window.addEventListener("keydown", (e) => {
    if (e.metaKey || e.ctrlKey) {
      switch (e.key) {
        case 'b':
          e.preventDefault();
          modify('bold', false, null);
          break;
        case 'i':
          e.preventDefault();
          modify('italic', false, null);
          break;
        case 'u':
          e.preventDefault();
          modify('underline', false, null);
          break;
        case 'z':
          if (e.shiftKey) {
            e.preventDefault();
            modify('redo', false, null);
          } else {
            e.preventDefault();
            modify('undo', false, null);
          }
          break;
        case 'k':
          e.preventDefault();
          modify('createLink', false, prompt('Enter URL:'));
          break;
        case 'j':
          e.preventDefault();
          modify('justifyFull', false, null);
          break;
        case 'c':
          e.preventDefault();
          modify('justifyCenter', false, null);
          break;
        case 'r':
          e.preventDefault();
          modify('justifyRight', false, null);
          break;
        case 'l':
          e.preventDefault();
          modify('justifyLeft', false, null);
          break;
      }
    }

  });

  optionsButtons.forEach((button) => {
    button.addEventListener("click", () => {
      modify(button.id, false, null);
    });
  });

  advancedButtons.forEach((button) => {
    button.addEventListener("change", () => {
      const label = document.querySelector(`label[for="${button.id}"]`);
      label ? label.style.borderBottom = `3px solid ${button.value}` : "";
      modify(button.id, false, button.value);
    });
  });

  linkButton.addEventListener("click", () => {
    let userLink = prompt("enter a URL");

    if (/http/i.test(userLink)) {
      modify(linkButton.id, false, userLink);
    } else {
      userLink = "https://" + userLink;
      modify(linkButton.id, false, userLink);
    }
  });

});

</script>

